.contenido-contacto {
    @include mq2 {
        @include grid(2, 1rem);
        background-image: url(../img/granos_cafe.jpg);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 40%;
    }

}
.formulario {
    .campo {
        margin-bottom: 2rem;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    label {
        text-transform: uppercase;
        display: block;
        font-size: 2rem;
        font-weight: 900;
    }
    input:not([type="submit"]),
    select {
        width: 100%;
        padding: 1rem;
        border: 5px solid $cafef;
    }
    input[type="submit"] {
        background-color: $cafef;
        color: $blanco;
        font-weight: 900;
        text-transform: uppercase;
        font-size: 2rem;
        width: 100%;
        padding: 1rem;
        transition: all 1s;
        &:hover {
            cursor: pointer;
            background-color: $cafe;
            color: $negro;
            transform: scale(1.1);
        }
    }
}