.nuestro-menu {
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top,
    $cafef, $cafef);
    background-repeat: no-repeat;
    background-position: center 5rem, center 95%, 100%;
    background-size: 200%;
    padding: 15rem 5rem 20rem 5rem;
    @include mq2 {
        padding: 15rem 0 20rem 0;
        background-size: 80%, 80%, 100%;
    }
    @include mq3 {
        background-size: 60%, 60%, 100%;
    }
}
.grid-menu {
    @include mq2 {
        @include grid(2, 2rem)
    }
}
.lista-menu {
    color: $blanco;
    ul {
        padding: 0;
    }
    li {
        @include grid3(1rem);
        align-items: center;
    }
    p {
        font-weight: 900;
        color: $blanco;
    }
    span {
        color: $cafe;
        font-weight: 900;
        display: block;
    }
    .precio {
        color: $blanco;
        font-size: 3rem;
        font-weight: 900;
        text-align: center;
    }
}