/** SECCION GALERIA **/
.experiencia {
    margin-top: 5rem;
}
.galeria {
    @include grid(2, $espaciado);
    @include mq2 {
        @include grid(4, $espaciado);
    }
    .imagen {
        img {
            height: 30rem;
            width: 100%;
            object-fit: cover;
        }
        @include mq2 {
            &:nth-child(1) {
                grid-row: 1 / 3;
                img {
                    height: 61rem;
                }
            }
            &:nth-child(2) {
                grid-column: 2 / 4;
            }
            &:nth-child(4) {
                grid-row: 2 / 4;
                img {
                    height: 61rem;
                }
            }
            &:nth-child(5) {
                grid-column: 3 / 5;
            }
            &:nth-child(6) {
                grid-column: 3 / 5;
            }
            &:nth-child(7) {
                grid-row: 3 / 5;
                img {
                    height: 61rem;
                }
            }
            &:nth-child(8) {
                grid-column: 2 / 5;
            }
            &:nth-child(9) {
                grid-column: 1 / 3;
            }
            &:nth-child(10) {
                grid-column: 3 / 5;
            }
        }
    }
}