/**Empieza Css proyecto**/
.header {
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    height: 90rem;
    .barra {
        display: flex;
        flex-direction: column;
        text-align: center;
        @include mq2 {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    .logo {
        width: 50%;
        margin: 0 auto;
        @include mq2 {
            margin: 0;
            width: 20rem;
        }
    }
    &::before {
        content: '';
        background-color: $negro;
        opacity: 75%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .contenido-header {
        position: relative;
    }
    .texto-header {
        color: $blanco;
        font-weight: bold;
        text-transform: uppercase;
        position: relative;
        margin-top: 5rem;
        @include mq2 {
            margin-top: 20rem;
        }
    }
}
.nav-principal {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    @include mq2 {
        flex-direction: row;
    }
    .link-nav {
        color: $blanco;
        font-size: 2rem;
        font-weight: 900;
        text-transform: uppercase;
        @include mq2 {
            margin-left: 1.5rem;
        }
        &:first-of-type {
            margin-left: 0;
        }
        &:hover {
            color: $cafe;
        }
        &.activo {
            color: $cafe;
        }
    }
}
