.footer {
    background-color: $cafef;
    .footer-contenido {
        background-color: $blanco;
    }
    .copy {
        text-align: center;
        color: $blanco;
        margin: 2rem 0 0 0;
        padding-bottom: 1rem;
    }
    .flex-contacto {
        padding-top: 2rem;
        display: flex;
        flex-direction: column;
        text-align: center;
        @include mq2 {
            flex-direction: row;
            justify-content: space-around;
            padding-top: 7rem;
        }
        .info-contacto {
            h3 {
                color: $cafef;
                font-weight: 900;
            }
            a {
                color: $rojo;
                font-weight: 900;
            }
        }
    }
}