.contenido-nosotros {
    @include mq2 {
        @include grid(2, 3rem);
        align-items: center;
    }
    .imagen {
        border: 3rem solid $blanco;
        box-shadow: 5px 5px 5px 3px rgb(0 0 0 / 1);
        height: auto;
    }
    .texto {
        margin-top: 3rem;
        @include mq2 {
            margin-top: 0;
        }
        .parrafo {
            text-align: justify;
        }
        .texto-medio {
            text-align: left;
            font-family: $fuente_secundaria;
            font-size: 2rem;
            margin: 0;
            border-left: 5px solid $cafef;
            padding: 1.5rem 1rem;
        }
    }
}