.testimoniales {
    background-color: $blanco;
    margin-top: -15rem;
    padding: 2rem;
    @include mq2 {
        .testimoniales {
            padding: 10rem;
        }
    }
    .testimonial-contenedor {
        background-color: $gris;
        border-radius: .5rem;
    }
    @include mq1 {
        .testimonial-contenedor {
            margin: 0;
        }
    }
    @include mq2 {
        .testimonial-contenedor {
            margin: 0 20rem;
        }
    }
    .testimonial {
        text-align: justify;
        padding: 2rem;
        font-weight: 400;
    }
    .autor {
        text-align: right;
        padding: 1rem;
        font-size: 2rem;
        color: $cafef;
        font-weight: 700;
    }
}