html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    background-color: $cafec;
}
p {
    color: $negro;
    font-family: $fuente_parrafos;
    font-size: 1.6rem;
}
[class$="__contenedor"] {
    max-width: 120rem;
    margin: 0 auto;
    width: 90%;
}
a {
    text-decoration: none;
    color: $negro;
}
img,
picture {
    width: 100%;
    display: block;
}
h1, h2, h3 {
    text-align: center;   
    text-transform: uppercase;
}