/** SECCION NOSOTROS **/
.contenido-principal {
    position: relative;
    margin-top: -10rem;
    background-color: $blanco;
    padding: 2rem;
    @include mq2 {
        padding: 10rem;
    }
    p {
        text-align: center;
    }
}
h2 {
    color: $cafe;
    font-size: 1.8rem;
    span {
        color: $cafef;
        font-size: 3.6rem;
        display: block;
    }
    &::after {
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url(../img/cafe.svg);
        background-size: 10rem;
    }
}
.heading-blanco {
    @extend h2;
    color: $blanco;
    span {
        color: $blanco;
    }
    &::after {
        display: none;
    }
}
.grid-iconos {
    margin-top: 5rem;
    @include mq2 {
        @include grid (3, $espaciado)
    }
    .icono {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .icono h3 {
        color: $cafef;
        font-weight: 900;
    }
    .icono img {
        width: 15rem;
    }
}