/**Empieza Css proyecto**/
.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 90rem;
}
.header .barra {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .header .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header .logo {
    margin: 0;
    width: 20rem;
  }
}
.header::before {
  content: "";
  background-color: #1a1b15;
  opacity: 75%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.header .contenido-header {
  position: relative;
}
.header .texto-header {
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .texto-header {
    margin-top: 20rem;
  }
}

.nav-principal {
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
  }
}
.nav-principal .link-nav {
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 900;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .nav-principal .link-nav {
    margin-left: 1.5rem;
  }
}
.nav-principal .link-nav:first-of-type {
  margin-left: 0;
}
.nav-principal .link-nav:hover {
  color: #c39c63;
}
.nav-principal .link-nav.activo {
  color: #c39c63;
}

/**Header de cada pagina**/
.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}
@media (min-width: 768px) {
  .header-inicio {
    height: 70rem;
  }
}

.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}
@media (min-width: 768px) {
  .header-nosotros {
    height: 70rem;
  }
}

.header-proceso {
  background-image: url(../img/header_proceso.jpg);
  background-position: bottom;
}
@media (min-width: 768px) {
  .header-proceso {
    height: 70rem;
  }
}

.header-menu {
  background-image: url(../img/header_menu.jpg);
}
@media (min-width: 768px) {
  .header-menu {
    height: 70rem;
  }
}

.header-galeria {
  background-image: url(../img/header_galeria.jpg);
}
@media (min-width: 768px) {
  .header-galeria {
    height: 70rem;
  }
}

.header-contacto {
  background-image: url(../img/header_contacto.jpg);
}
@media (min-width: 768px) {
  .header-contacto {
    height: 70rem;
  }
}

/** SECCION NOSOTROS **/
.contenido-principal {
  position: relative;
  margin-top: -10rem;
  background-color: #FFFFFF;
  padding: 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
  }
}
.contenido-principal p {
  text-align: center;
}

h2, .heading-blanco {
  color: #c39c63;
  font-size: 1.8rem;
}
h2 span, .heading-blanco span {
  color: #5e311e;
  font-size: 3.6rem;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: #FFFFFF;
}
.heading-blanco span {
  color: #FFFFFF;
}
.heading-blanco::after {
  display: none;
}

.grid-iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .grid-iconos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.grid-iconos .icono {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.grid-iconos .icono h3 {
  color: #5e311e;
  font-weight: 900;
}
.grid-iconos .icono img {
  width: 15rem;
}

/** SECCION GALERIA **/
.experiencia {
  margin-top: 5rem;
}

.galeria {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .galeria {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria .imagen img {
  height: 30rem;
  width: 100%;
  object-fit: cover;
}
@media (min-width: 768px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 61rem;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 61rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 61rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
}

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 95%, 100%;
  background-size: 200%;
  padding: 15rem 5rem 20rem 5rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
    background-size: 80%, 80%, 100%;
  }
}
@media (min-width: 1024px) {
  .nuestro-menu {
    background-size: 60%, 60%, 100%;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.lista-menu {
  color: #FFFFFF;
}
.lista-menu ul {
  padding: 0;
}
.lista-menu li {
  display: grid;
  gap: 1rem;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}
.lista-menu p {
  font-weight: 900;
  color: #FFFFFF;
}
.lista-menu span {
  color: #c39c63;
  font-weight: 900;
  display: block;
}
.lista-menu .precio {
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 900;
  text-align: center;
}

.testimoniales {
  background-color: #FFFFFF;
  margin-top: -15rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .testimoniales .testimoniales {
    padding: 10rem;
  }
}
.testimoniales .testimonial-contenedor {
  background-color: #e1e1e1;
  border-radius: 0.5rem;
}
@media (min-width: 480px) {
  .testimoniales .testimonial-contenedor {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .testimoniales .testimonial-contenedor {
    margin: 0 20rem;
  }
}
.testimoniales .testimonial {
  text-align: justify;
  padding: 2rem;
  font-weight: 400;
}
.testimoniales .autor {
  text-align: right;
  padding: 1rem;
  font-size: 2rem;
  color: #5e311e;
  font-weight: 700;
}

.footer {
  background-color: #5e311e;
}
.footer .footer-contenido {
  background-color: #FFFFFF;
}
.footer .copy {
  text-align: center;
  color: #FFFFFF;
  margin: 2rem 0 0 0;
  padding-bottom: 1rem;
}
.footer .flex-contacto {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .flex-contacto {
    flex-direction: row;
    justify-content: space-around;
    padding-top: 7rem;
  }
}
.footer .flex-contacto .info-contacto h3 {
  color: #5e311e;
  font-weight: 900;
}
.footer .flex-contacto .info-contacto a {
  color: #b60000;
  font-weight: 900;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.contenido-nosotros .imagen {
  border: 3rem solid #FFFFFF;
  box-shadow: 5px 5px 5px 3px black;
  height: auto;
}
.contenido-nosotros .texto {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenido-nosotros .texto {
    margin-top: 0;
  }
}
.contenido-nosotros .texto .parrafo {
  text-align: justify;
}
.contenido-nosotros .texto .texto-medio {
  text-align: left;
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  margin: 0;
  border-left: 5px solid #5e311e;
  padding: 1.5rem 1rem;
}

@media (min-width: 768px) {
  .proceso-cafe {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
  }
}
.proceso-cafe .imagen,
.proceso-cafe .texto {
  flex: 1;
}
.proceso-cafe:nth-child(even) {
  flex-direction: row-reverse;
}
.proceso-cafe .imagen {
  height: auto;
}
@media (min-width: 768px) {
  .proceso-cafe .texto {
    margin: 0 2rem;
  }
}
.proceso-cafe .texto h3 {
  color: #5e311e;
  font-size: 1.8rem;
  font-weight: 900;
  text-align: justify;
  margin: 0;
  margin-top: 1rem;
}
@media (min-width: 1024px) {
  .proceso-cafe .texto h3 {
    font-size: 3.5rem;
  }
}
.proceso-cafe .texto h3::after {
  content: "";
  display: block;
  width: 30%;
  height: 5px;
  background-color: #c39c63;
}
.proceso-cafe .texto p {
  text-align: justify;
}

.cont-menu {
  padding-bottom: 0;
}

@media (min-width: 768px) {
  .contenido-menu {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: flex-end;
  }
}
.contenido-menu .texto p {
  text-align: justify;
  margin: 0;
  padding: 2rem 0;
}

@media (min-width: 768px) {
  .contenido-contacto {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario label {
  text-transform: uppercase;
  display: block;
  font-size: 2rem;
  font-weight: 900;
}
.formulario input:not([type=submit]),
.formulario select {
  width: 100%;
  padding: 1rem;
  border: 5px solid #5e311e;
}
.formulario input[type=submit] {
  background-color: #5e311e;
  color: #FFFFFF;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 2rem;
  width: 100%;
  padding: 1rem;
  transition: all 1s;
}
.formulario input[type=submit]:hover {
  cursor: pointer;
  background-color: #c39c63;
  color: #1a1b15;
  transform: scale(1.1);
}