.proceso-cafe {
    @include mq2 {
        display: flex;
        margin-bottom: 2rem;
        align-items: center;
    }
    .imagen,
    .texto {
        flex: 1;
    }
   &:nth-child(even) {
       flex-direction: row-reverse;
   }
   .imagen {
       height: auto;
   }
   .texto {
       @include mq2 {
           margin: 0 2rem;
       }
       h3 {
           color: $cafef;
           font-size: 1.8rem;
           font-weight: 900;
           text-align: justify;
           margin: 0;
           margin-top: 1rem;
           @include mq3 {
               font-size: 3.5rem;
           }
           &::after {
               content: '';
               display: block;
               width: 30%;
               height: 5px;
               background-color: $cafe;
           }
       }
       p {
           text-align: justify;
       }
   }
}